.navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  z-index: 1;
}

.navigation__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  width: 100%;
}

.navigation__nav-links {
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
  width: 100%;
  margin: 0;
}

.navigation__nav-link {
  color: #fff;
  text-decoration: none;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.navigation__nav-link:hover,
.navigation__link-account:hover,
.navigation__burger-menu-button:hover {
  opacity: 0.7;
}

.navigation__link-account {
  display: none;
}

.navigation__signin {
  display: none;
}

.navigation__signout {
  justify-content: right;
}

.navigation__nav-links-list {
  display: flex;
  flex-direction: row;
  gap: 19px;
  align-items: flex-start;
  justify-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0 0 0 65px;
}

.navigation__nav-link_bacic {
  border-bottom: 1px solid #fff;
  padding: 0 0 8px 0;
}

.navigation__burger-menu-button {
  background-image: url(../../images/burger.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: 44px;
  height: 44px;
  cursor: pointer;
  display: block;
  margin: 0 0 0 20px;
}

.navigation__burger-menu-hidden {
  display: none;
  z-index: 10;
}

.navigation__burger-menu-button:focus + .navigation__burger-menu-hidden {
  display: flex;
}

@media screen and (min-width: 320px) {
  .movies-cards-list__list {
      grid-template-columns: repeat(auto-fit, minmax(339px, 1fr));
      gap: 47px 30px;
  }
}

@media screen and (min-width: 768px) {
  .navigation__signin {
      display: flex;
      justify-content: center;
  }

  .navigation__link-account {
      width: 100px;
      padding: 8px 6px;
      background-color: #313131;
      border-radius: 20px;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 16px;
  }

  .navigation__burger-menu-button {
      display: none;
  }
}
