.promo {
  background-color: #272727;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 292px; 
  margin: 0 auto;
  padding: 0;
}

.promo__title {
  text-align: center;
  font-size: 29px;
  font-style: normal;
  font-weight: 400;
  line-height: 37px;
  letter-spacing: -1.16px;
  margin: 220px 0 0 0;
}

@media screen and (min-width: 465px) {
  .promo {
      max-width: 750px; 
      padding: 0 0 28px 0;
  }

  .promo__title {
      font-size: 40px;
      line-height: 52px;
      letter-spacing: -1.6px;
      padding: 130px 30px 0 30px;
  }
}

@media screen and (min-width: 465px) {
  .promo {
      max-width: 1200px;
  }

  .promo__title {
      font-size: 50px;
      line-height: 58px;
      letter-spacing: -2px;
      max-width: 730px;
      margin: 30px 0 0 0;
  }
}
