.movies-card {
  display: grid;
  grid-template-columns: auto 54px;
  grid-template-rows: auto auto;
  row-gap: 14px;
  position: relative;
  cursor: pointer;
}

.movies-card__image {
  object-fit: cover;
  grid-column: 1/-1;
  width: 100%;
  height: 168px;
  width: 300px;
  border-radius: 6px;
}

.movies-card__title {
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  white-space: nowrap;
  margin: 0;
}

.movies-card__time {
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 10px;
  color: #8b8b8b;
  background-color: #2f2f2f;
  /* margin: 0 14px 0 0;*/
  padding: 3px 0 0 0;
  text-align: center;
  border-radius: 3px;
  width: 52px;
  height: 15px;
}

.movies-card__button-to-save {
  position: absolute;
  top: 14px;
  right: 14px;
  cursor: pointer;
  border: 0;
  background-color: transparent;
  opacity: 0;
}

.movies-card__button-to-save_dell {
  background-image: url(../../../images/dell_d3.svg);
  background-size: contain;
  background-repeat: no-repeat;
  width: 21px;
  height: 21px;
  align-self: center;
}

.movies-card:hover .movies-card__button-to-save_dell {
  opacity: 1;
}

@media screen and (min-width: 385px) {
  .movies-card {
      max-width: 339px;
      grid-template-columns: auto 54px;
      grid-template-rows: auto auto;
  }
  .movies-card__image {
      height: 190px;
      width: 339px;
  }
  .movies-card__title {
      margin: 10px 0 0 0;
  }

  .movies-card__time {
      margin: 10px 0 0 0;
  }
}

@media screen and (min-width: 768px) {
  .movies-card {
      max-width: 360px;
      height: 201px;
      grid-template-columns: auto 54px 14px;
      grid-template-rows: auto auto;
      row-gap: 14px;
  }
  .movies-card__image {
      height: 201px;
      width: 360px;
  }

  .movies-card__title {
      margin: 0 0 0 14px;
  }

  .movies-card__time {
      margin: 0 14px 0 0;
  }
}
